.carousel {
  /* width: 300px; */
  overflow: hidden;
  position: relative;
  /* margin: auto; */
  background-color: #1b45a9;

  height: 10vh;
}

.carousel-inner {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-item {
  min-width: 100%;
  box-sizing: border-box;
  max-height: 10vh;
  display: flex;
  justify-content: center;
}

.carousel-item img {
  height: 8vh;
  display: block;
  padding: 5px 0px 5px 0px;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
}

.carousel-button.prev {
  left: 10px;
}

.carousel-button.next {
  right: 10px;
}

.carousel-button:focus {
  outline: none;
}
