@media only screen and (min-width: 500px) {
  .notification-alert {
    position: absolute;
    background-color: rgb(255, 81, 8);
    top: 14px;
    /* right: 175px; */
    border-radius: 50%;
    height: 15px;
    width: 15px;
    font-size: 10px;
    text-align: center;
    color: white;
  }
}

@media only screen and (max-width: 500px) {
  .notification-alert {
    position: absolute;
    background-color: rgb(255, 81, 8);
    top: 14px;
    left: 265px;
    border-radius: 50%;
    height: 15px;
    width: 15px;
    font-size: 10px;
    text-align: center;
    color: white;
  }
}

