  color: #27b127;
}

.nombre-campo-juego-fixture {
  font-size: 14;
}

.nombre-campo-juego-fixture {
  font-size: 12;
}

.live {
  width: 15px;
  height: 15px;
  display: inline-block;
  background: #25bf00;
  border-radius: 20px;
  position: relative;
  top: 2px;
  right: 6px;
}

.cool-link {
  color: #666;
  display: inline-block;
  margin: 0;
  text-transform: uppercase;
}

.cool-link:after {
  display: block;
  content: "";
  border-bottom: solid 3px #3bb900;
  transform: scaleX(0);
  animation: underline 2s infinite; /* Animación activa siempre */
}

@keyframes underline {
  0% {
    transform: scaleX(0);
  }
  50% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}

.resultado-mobile {
  font-size: 14px;
}

.container-ad-div-item {
  position: relative;
  margin-top: 1vh;
}

.container-ad-div-item img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}
::-webkit-scrollbar {
  width: 3px;
  height: 4px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  background-color: #989898;
}

.d-block {
  display: block;
}

.live-result {
  color: rgb(1, 140, 1);
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Aplicar la animación al elemento */
.live-result {
  animation: blink 1.5s infinite;
}

.live-circle {
  height: 10px;
  width: 10px;
  margin-right: 2px;
  display: inline-block;
  border-radius: 10px;
  background-color: rgb(3, 189, 3);
}

.live-text {
  color: rgb(3, 189, 3);
  font-size: 10px;
  font-weight: 500;
}

.instancia-fecha {
  background-color: white;
  border-top: solid 1px lightgray;
  border-radius: 2px;
  font-size: 12px;
}
